export default {

	name: "latte-button-dropdown",

	props: {
		ariaLabel: {default: "", type: String},
		buttonClass: {default: "btn-text btn-icon btn-dark", type: String},
		icon: {default: "", type: String},
		iconAfter: {default: "", type: String},
		iconBefore: {default: "", type: String},
		label: {default: "", type: String},
		marginX: {default: -9, type: Number},
		marginY: {default: 0, type: Number},
		small: {default: false, type: Boolean},
		tooltip: {default: "", type: String}
	},

	data()
	{
		return {
			isOpen: false,
			isRipple: false
		};
	},

	created()
	{
		this.isRipple = this.$parent.$options.name === "latte-ripple";
	},

	computed: {

		avatarUrl()
		{
			if (this.icon.substr(0, 6) === "avatar")
				return this.icon.substr(7);

			return null;
		},

		buttonClasses()
		{
			const classes = this.buttonClass.split(" ");

			if (this.small)
				classes.push("btn-sm");

			if (this.isOpen)
				classes.push("is-open", "tooltip-disabled");

			if (this.isRipple)
				classes.push("is-ripple");

			return classes;
		},

		iconClasses()
		{
			return ["mdi", `mdi-${this.icon}`];
		},

		iconAfterClasses()
		{
			return ["mdi", `mdi-${this.iconAfter}`];
		},

		iconBeforeClasses()
		{
			return ["mdi", `mdi-${this.iconBefore}`];
		},

		self()
		{
			return this;
		}

	},

	methods: {

		onClose()
		{
			this.isOpen = false;
		},

		onOpen()
		{
			this.isOpen = true;
		}

	}

}

